import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
  tableRow: {
    height: 10
  }
});

function createData(name, Points) {
  return { name, Points};
}

const rows = [
  createData('Bala', 7),
  createData('Deepak, Sumit, Diljish', 6),
  createData('Rishabh', 5),
  createData('Anjana, Hanoop, Ram', 4),
  createData('Biju, Jeetesh, Sandeep, Jayakrishnan, Anusha', 3),
  createData('JK, Shine, Nansilima, Jemey, Abhiram, Ananthu, Suchithra, Binnya, Nithu, Hridya', 2),
  createData('Lekshmi, Namita, Kannan, Riya, Anjitha, Veena, Akhil', 1)
];

export default function BasicTable() {
  const classes = useStyles();

  const myStyle = { height: "10px", padding: "5px"};

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow style={myStyle} >
            <TableCell style={myStyle}>Name</TableCell>
            <TableCell style={myStyle} align="center">Points</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow style={myStyle} key={row.name} className={classes.tableRow}>
              <TableCell style={myStyle} component="th" scope="row">
                {row.name}
              </TableCell>
              <TableCell style={myStyle} align="center">{row.Points}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
